#taskbar-cont{
    width:100%;
    height:50px;
    position: absolute;
    bottom:0;
    z-index: 8790287;
    display: -ms-flexbox;
    display: flex;
}
#taskbar{
    width:100%;
    height:100%;
    display: -ms-flexbox;
    display: flex;
}
@media only screen and (max-width: 720px) {
    #taskbar{
        height:30px;
    }
}


